import React from 'react'
import styles from './UploadFile.module.scss'
import uploadImage from '../../assets/img/thumb.svg'
import { HiOutlineHashtag } from 'react-icons/hi'
import { FiAtSign } from 'react-icons/fi'
import Button from '../common/Button'

const UploadFile = () => {
    return (
        <div className={styles.wrapper}>
            <div className={styles.container}>
                <div className={styles.title}>
                    Tải video lên
                    <span className={styles.subTitle}>Video này sẽ được công bố cho @vanphuocdo0</span>
                </div>
                <div className={styles.content}>
                    <div className={styles.upload}>
                        <div className={styles.card}>
                            <img src={uploadImage} alt="upload image" className={styles.image} />
                            <div className={styles.textMain}>Chọn video để tải lên</div>
                            <div className={`${styles.textSub} ${styles.textSubMargin}`}>Hoặc kéo và thả tập tin</div>
                            <br />
                            <ul className={`${styles.textSub} ${styles.textSubMenu}`}>
                                <li>MP4 hoặc WebM</li>
                                <li>Độ phân giải 720x1280 trở lên</li>
                                <li>Tối đa 180 giây</li>
                            </ul>
                        </div>
                        <input
                            type="file"
                            name="upload-btn"
                            id=""
                            accept="video/mp4,video/x-m4v,video/*"
                            className={styles.uploadBtn}
                        />
                    </div>
                    <div className={styles.form}>
                        <div className={styles.captionWrap}>
                            <div className={styles.container}>
                                <div>
                                    <div className={styles.textContainer}>
                                        <div className={styles.font}>Chú thích</div>
                                        <div className={styles.requireFont}>
                                            <span>0/150</span>
                                        </div>
                                    </div>
                                    <div className={styles.gapTop}>
                                        <div className={styles.containerInside}>
                                            <div className={styles.editor}>
                                                <div
                                                    className={`${styles.draftEditorRoot} ${styles.draftEditorAlignLeft}`}
                                                >
                                                    <div className={styles.draftEditorContainer}>
                                                        <div
                                                            aria-autocomplete="list"
                                                            aria-expanded="false"
                                                            className={styles.publicDraftEditorContent}
                                                            contentEditable
                                                            role="combobox"
                                                            spellCheck="false"
                                                        ></div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className={`${styles.iconStyle} ${styles.at}`}>
                                                <FiAtSign className={styles.icon} />
                                            </div>
                                            <div className={`${styles.iconStyle} ${styles.hash}`}>
                                                <HiOutlineHashtag className={styles.icon} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className={`${styles.containerAt} ${styles.hideDisplay}`}></div>
                        </div>
                        <div className={styles.title}>Ảnh bìa</div>
                        <div className={`${styles.containerIn} ${styles.empty}`}>
                            <div className={styles.bgContainer}>
                                <div className={`${styles.candidate} ${styles.empty}`}></div>
                            </div>
                            <div className={`${styles.chosen} ${styles.empty}`}></div>
                        </div>
                        <div className={styles.formItem}>
                            <div className={styles.permission}>
                                <div className={styles.titledesc}>Ai có thể xem video này</div>
                                <div className={styles.radioGroup}>
                                    <label htmlFor="public" className={styles.radio}>
                                        <input type="radio" id="public" value="0" className={styles.inputCheck} />
                                        <span className={`${styles.radioBox} ${styles.checked}`}></span>
                                        Công khai
                                    </label>
                                    <label htmlFor="public" className={styles.radio}>
                                        <input type="radio" id="public" className={styles.inputCheck} />
                                        <span className={`${styles.radioBox}`}></span>
                                        Bạn bè
                                    </label>
                                    <label htmlFor="public" className={styles.radio}>
                                        <input type="radio" id="public" className={styles.inputCheck} />
                                        <span className={`${styles.radioBox}`}></span>
                                        Riêng tư
                                    </label>
                                </div>
                            </div>
                            <div className={styles.permission}>
                                <div className={styles.titledesc}>Cho phép người dùng</div>
                                <div className={styles.checkboxContainer}>
                                    <label htmlFor="public" className={styles.checkbox}>
                                        <input type="radio" id="public" value="0" className={styles.inputCheck} />
                                        <span
                                            className={`${styles.radioCheck} ${styles.radioBox} ${styles.checked}`}
                                        ></span>
                                        Công khai
                                    </label>
                                    <label htmlFor="public" className={styles.checkbox}>
                                        <input type="radio" id="public" className={styles.inputCheck} />
                                        <span className={`${styles.radioCheck} ${styles.radioBox}`}></span>
                                        Bạn bè
                                    </label>
                                    <label htmlFor="public" className={styles.checkbox}>
                                        <input type="radio" id="public" className={styles.inputCheck} />
                                        <span className={`${styles.radioCheck} ${styles.radioBox}`}></span>
                                        Riêng tư
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div className={styles.part}>
                            <Button children="Hủy bỏ" type="default" borderOpacity color="blackOpacity" size="ls" />
                            <Button children="Đăng" bgPrimary colorGrey size="ls" marginLeft />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default UploadFile
